body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    padding: 0px;
    margin: 0px;
    overflow: hidden;
}

.main {
    background-color: #725DD5;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 10px calc(100% - 60px) 10px;
    overflow: hidden;
}

.main img {
    margin: 10px;
}

.myInformation {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50vw;
}

.myImage img {
    width: 150px;
}

.myName {
    display: flex;
    grid-gap: 5px;
    gap: 5px;
    color: #A4ADFD;
    font-size: 50px;
}

.headName {
    color: white;
}

.sureName {
    font-style: italic;
}

.email {
    color: white;
    font-size: x-large;
}

.btnOne {
    padding: 10px 20px;
    margin: 20px;
    border-radius: 50px;
    border: 0px;
    cursor: pointer;
    font-weight: bolder;
}

.circleMain {
    position: absolute;
    border-radius: 50%;
    outline: 20px solid #7D63FA;
    outline-offset: -15px;
    background-color: #7D63FA;
    right: 0px;
    width: 50%;
    height: 100%;
    display: flex;
    padding: 20px;
    -webkit-transform: scaleX(200%) scaleY(200%) translateX(30%);
            transform: scaleX(200%) scaleY(200%) translateX(30%);
    -webkit-animation: imgser 60s ease-in-out 0s infinite;
            animation: imgser 60s ease-in-out 0s infinite;
}

.circleMain::after {
    content: "Akshansh";
    
}

.circleMain img {
    -webkit-filter: blur(20px);
            filter: blur(20px);
    width: 100%;
    height: 100%;
    -webkit-animation: imgser 60s ease-in-out 0s infinite;
            animation: imgser 60s ease-in-out 0s infinite;
}

@-webkit-keyframes imgser {
    100% {
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}

@keyframes imgser {
    100% {
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}
